.loading {
  height: 100vh;
  width: 100%;
  z-index: 99;
  background: radial-gradient(circle, #240005 6%, #79090d 39%, #770505 100%);
  place-items: center;
  font-family: Orbitron, sans-serif;
  display: grid;
  position: absolute;
  inset: 0;
}

.loading_box {
  width: 500px;
  height: 200px;
  border: 5px groove #5aafaf;
  border-color: #5aafaf #6cdaff;
  position: relative;
}

@media (max-width: 600px) {
  .loading_box {
    width: 90vw;
  }
}

.loading_bar {
  width: 90%;
  height: 10px;
  background: #ccc;
  border-radius: 3px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.loading_bar--inner {
  height: 100%;
  width: 0;
  background: #6cdaff;
  border-radius: 2px;
}

.loading_text {
  color: #fff;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  font-size: 20px;
  font-weight: 700;
  display: flex;
  position: relative;
}

@media (max-width: 350px) {
  .loading_text {
    font-size: 15px;
  }
}

.loading_text--dot {
  width: 15px;
  height: 15px;
  background: #fff;
  border-radius: 50%;
  margin: 0 3px;
  animation: 1s infinite pulse;
}

@keyframes pulse {
  from {
    opacity: 0;
    background: #6cdaff;
  }

  to {
    opacity: 1;
  }
}

.loading_text--border {
  width: 85%;
  height: 1px;
  background: #5aafaf;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.loading_counter {
  color: #fff;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  font-size: 20px;
  font-weight: 700;
  display: flex;
  position: relative;
  top: 70%;
}

.loading_counter--number {
  color: #5aafaf;
}

.loading_width_anim {
  animation: 1s forwards width_animation;
}

@keyframes width_animation {
  0% {
    width: 85%;
  }

  100% {
    width: 270px;
  }
}

.loading_svg {
  width: 105%;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

