.landing {
  width: 100%;
  height: 100vh;
  color: #fff;
  background: 55% 0 / cover no-repeat;
  position: relative;
  overflow: hidden;
}

.landing .header {
  z-index: 2;
  position: absolute;
  top: -100%;
  left: 50%;
  transform: translateX(-50%);
}

.landing .header_wrap {
  width: 60vw;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.landing .header_left {
  align-items: center;
  gap: 2rem;
  display: flex;
}

.landing .header_left li {
  font-size: 18px;
  font-weight: 700;
}

@media (max-width: 1200px) {
  .landing .header_wrap {
    width: 100vw;
  }
}

@media (max-width: 650px) {
  .landing .header_sm {
    display: none;
  }
}

@media (max-width: 370px) {
  .landing .header {
    left: 36%;
  }

  .landing .header_wrap {
    gap: 20%;
  }

  .landing .header .coolLink[data-text="Home"] {
    display: none;
  }
}

.landing .socials {
  z-index: 2;
  align-items: center;
  gap: 1rem;
  display: flex;
  position: absolute;
  bottom: -50%;
  left: 50%;
  transform: translateX(-50%);
}

.landing .scrollDown {
  position: absolute;
  bottom: -50%;
  left: 50%;
  transform: translateX(-50%);
}

.landing .scrollDown_wheel {
  height: 42px;
  width: 24px;
  color: #fff;
  border: 2px solid;
  border-radius: 10px;
}

.landing .scrollDown_wheel--inner {
  width: 4px;
  height: 4px;
  background: #fff;
  border-radius: 50%;
  margin-top: 5px;
  animation: .5s linear infinite movingwheel;
  position: absolute;
  left: 10px;
}

@keyframes movingwheel {
  0% {
    top: 1px;
  }

  25% {
    top: 2px;
  }

  50% {
    top: 3px;
  }

  75% {
    top: 2px;
  }

  100% {
    top: 1px;
  }
}

.landing .scrollDown .scrollDown_arrows {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.landing .scrollDown .scrollDown_arrows .scrollDown_arrow {
  width: 15px;
  height: 15px;
  background-repeat: no-repeat;
  border-bottom: 2px solid #fff;
  border-right: 2px solid #fff;
  animation: 1s infinite alternate scrollAnim;
  transform: rotate(45deg);
}

.landing .scrollDown .scrollDown_arrows .scrollDown_arrow:nth-child(1) {
  animation-delay: .1s;
}

.landing .scrollDown .scrollDown_arrows .scrollDown_arrow:nth-child(2) {
  animation-delay: .2s;
}

.landing .scrollDown .scrollDown_arrows .scrollDown_arrow:nth-child(3) {
  animation-delay: .3s;
}

@keyframes scrollAnim {
  0% {
    opacity: 0;
  }

  50% {
    opacity: .5;
  }

  100% {
    opacity: 1;
  }
}

.landing .intro {
  width: 100%;
  height: 50%;
  z-index: 2;
  font-family: gabriel-sans-trial, sans-serif;
  position: absolute;
  top: 20%;
