.coolLink {
  place-items: center;
  font-size: 2vw;
  display: grid;
  position: relative;
  overflow: hidden;
}

.coolLink:before {
  content: "";
  width: 100%;
  height: 2px;
  transform-origin: 0;
  background: currentColor;
  transition: transform .4s cubic-bezier(.5, .5, .3, 1);
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scale3d(0, 1, 1);
}

.coolLink:after {
  content: attr(data-text);
  height: 100%;
  transition: transform .4s cubic-bezier(.5, .5, .3, 1);
  position: absolute;
  top: 0;
  transform: translate3d(150%, -150px, 0);
}

.coolLink span {
  transition: transform .4s cubic-bezier(.5, .5, .3, 1);
  transform: translate3d(0, 0, 0);
}

.coolLink:hover:before {
  transform-origin: 100%;
  transform: scale3d(1, 1, 1);
}

.coolLink:hover:after {
  transform: translate3d(0, 0, 0);
}

.coolLink:hover span {
  transform: translate3d(-150%, 150px, 0);
}

@media (max-width: 800px) {
  .coolLink {
    font-size: 3vw;
  }
}

@media (max-width: 600px) {
  .coolLink {
    font-size: 5vw;
  }
}

.coolButton {
  cursor: pointer;
  width: 175px;
  height: 120px;
  color: #fff;
  z-index: 2;
  background: none;
  border: none;
  position: relative;
}

.coolButton:before, .coolButton:after {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.coolButton:before {
  content: "";
  clip-path: path("M154.5,88.5 C131,113.5 62.5,110 30,89.5 C-2.5,69 -3.5,42 4.5,25.5 C12.5,9 33.5,-6 85,3.5 C136.5,13 178,63.5 154.5,88.5 Z");
  background: #fff;
  transition: clip-path .5s cubic-bezier(.585, 2.5, .645, .55);
}

.coolButton:hover:before {
  clip-path: path("M143,77 C117,96 74,100.5 45.5,91.5 C17,82.5 -10.5,57 5.5,31.5 C21.5,6 79,-5.5 130.5,4 C182,13.5 169,58 143,77 Z");
}

.coolButton span {
  mix-blend-mode: difference;
  font-weight: 500;
}

.coolButton:after {
  content: "";
  height: 85%;
  width: 90%;
  border: 1px solid #fff;
  border-radius: 58% 42% 55% 45% / 56% 45% 55% 44%;
  transition: transform .5s cubic-bezier(.585, 2.5, .645, .55);
  top: 6px;
  transform: rotate(-20deg);
}

.coolButton:hover:after {
  transform: translate3d(0, -5px, 0);
}

@media (max-width: 800px) {
  .coolButton {
    font-size: 23px;
    transform: scale3d(.7, .7, .7);
  }
}

.coloredIcon {
  color: #fff;
  font-size: 35px;
}

.coloredIcon:hover {
  animation: .2s infinite coloredIconAnimation;
}

@keyframes coloredIconAnimation {
  0% {
    text-shadow: 5px 4px #f44336, -5px -6px #2196f3;
  }

  25% {
    text-shadow: -5px -6px #f44336, 5px 4px #2196f3;
  }

  50% {
    text-shadow: 5px -4px #f44336, -8px 4px #2196f3;
  }

  75% {
    text-shadow: -8px -4px #f44336, -5px -4px #2196f3;
  }

  100% {
    text-shadow: -5px 0 #f44336, 5px -4px #2196f3;
  }
}

.coolCircleEyeButton {
  z-index: 2;
  display: inline-block;
  position: relative;
}

.coolCircleEyeButton .textcircle {
  width: 85%;
  display: block;
}

.coolCircleEyeButton .textcircle text {
  text-transform: uppercase;
  fill: #000;
  font-size: 32px;
}

@keyframes rotate {
  to {
    transform: rotate(-360deg);
  }
}

.coolCircleEyeButton:hover .textcircle {
  animation: 7s linear infinite rotate;
}

.coolCircleEyeButton:hover .textcircle text {
  font-weight: 600;
}

.coolCircleEyeButton:hover .eye__inner, .coolCircleEyeButton:hover .eye__iris, .coolCircleEyeButton:hover .eye__lashes-up {
  animation: 2s step-end infinite hide;
}

@keyframes hide {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 1;
  }
}

.coolCircleEyeButton:hover .eye__lashes-down {
  animation: 2s step-end infinite show;
}

@keyframes show {
  0% {
    opacity: 1;
  }

  10% {
    opacity: 0;
  }
}

.eye {
  width: 60px;
  height: 60px;
  margin-left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-90%, -50%);
}

.eye__outer, .eye__inner, .eye__lashes-up, .eye__lashes-down {
  stroke: #000;
  fill: none;
  stroke_width: 1.5px;
}

.eye__lashes-down {
  opacity: 0;
}

